import React from 'react';
import Layout from '../components/layout';
import SearchEngineOptimization from '../components/seo';
import { graphql } from 'gatsby';
import { GatsbyImage } from "gatsby-plugin-image";

export const query = graphql`query ($slug: String!) {
  markdownRemark(fields: {slug: {eq: $slug}}) {
    frontmatter {
      title
      description
      date(formatString: "MMMM DD, YYYY")
      thumbnail {
        childImageSharp {
          gatsbyImageData(quality: 80, layout: FULL_WIDTH, placeholder: BLURRED, backgroundColor: "white")
        }
      }
    }
    html
  }
}
`;

const Blog = (props) => {
  return <>
    <SearchEngineOptimization pageTitle={`Blog - ${props?.data.markdownRemark.frontmatter.title}`}
      description={props?.data.markdownRemark.frontmatter.description} />
    <Layout>
			<div class='blog-header' style={{ display: "grid" }}>
				<GatsbyImage
					style={{
						gridArea: "1/1",
						maxHeight: 400,
					}}
					layout="fullWidth"
					aspectRation={3 / 1}
					alt=""
					formats={["auto", "webp", "avif"]}
					image={props.data.markdownRemark.frontmatter.thumbnail.childImageSharp.gatsbyImageData}
				/>
        <div style={{
          // By using the same grid area for both, they are stacked on top of each other
          gridArea: "1/1",
          position: "relative",
          // This centers the other elements inside the hero component
          placeItems: "center",
          display: "grid",
          }}
				>
          <h6 className='date'>
            {props.data.markdownRemark.frontmatter.date}
          </h6>
          <h2 className='title'>{props.data.markdownRemark.frontmatter.title}</h2>
        </div>
			</div>

      <div className='blog-container section container'>
        <div className='blog-content' dangerouslySetInnerHTML={{ __html: props.data.markdownRemark.html }}></div>
      </div>

    </Layout>
  </>;
};

export default Blog;
